@import url("https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&family=Iceberg&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --maincolor: rgb(108, 99, 255);
}
.btn:hover {
  cursor: pointer;
  background-color: var(--maincolor);
  box-shadow: 1px 1px 6px var(--maincolor);
  transition: 0.5s ease-out;
  color: white;
}
.Impmsg {
  display: none;
}
@media (max-width: 480px) {
  .AppComp {
    display: none;
  }
  h1 {
    display: block !important;
    text-align: center;
    color: red;
  }
  h1:first-child {
    margin-top: 45vh;
  }
}
.navbar {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  height: 10vh;
}
.bright .navbar {
  /* background-color: #005; */
  box-shadow: 1px 1px 10px var(--maincolor);
}
.dark .navbar {
  position: relative;
  z-index: 5;
  background-color: #222831;
  box-shadow: 1px 1px 6px white;
}
.navbar ul {
  display: flex;
  align-items: center;
  gap: 25px;
  margin-left: 25px;
}
.navbar ul li {
  list-style: none;
  font-weight: bold;
}
.bright .navbar ul li{
  color: black;
}
.dark .navbar ul li {
  color: var(--maincolor);
}
.navbar ul li:hover {
  cursor: pointer;
}
.navbar .searchbar {
  display: flex;
  align-items: center;
  gap: 25px;
  height: 100%;
}
.searchinput {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50%;
  border: 1px solid black;
  padding: 2px;
  border-radius: 8px;
  width: 18vw;
}
.searchinput:hover{
  border: 2px solid var(--maincolor) !important;
}
.dark .searchinput {
  background-color: white;
}
.searchinput img {
  height: 20px;
  width: 20px;
}
.navbar button {
  height: 50%;
  padding: 0 10px 0 10px;
  border-radius: 7px;
  border: 1px solid black;
  background-color: white;
}
.navbar input {
  height: 100%;
  border: none;
  outline: none;
  margin-left: 5px;
  width: 100%;
}
.modeIcon {
  display: flex;
  align-items: center;
}
.navbar .modeIcon img {
  height: 40px;
  width: 40px;
  margin-right: 25px;
}
.navbar .modeIcon img:hover {
  cursor: pointer;
}
.list {
  text-decoration: none;
}

/* ************************* Foooter *****************************************  */
.footer {
  height: 7.5vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  position: relative;
  z-index: 5;
}
.bright .footer {
  box-shadow: inset 1px 1px 36px var(--maincolor);
}
.dark .footer {
  background-color: #222831;
  color: white;
}

/* **************************** About **************************** */

.aboutContainer {
  height: 82.5vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 5px;
}
.aboutContainer .heading h1,
.aboutContainer h1 span {
  margin-top: 10px;
  font-size: xxx-large;
  font-family: iceberg;
}
.aboutContainer .heading h1 span {
  color: var(--maincolor);
}
.dark .aboutContainer {
  background-color: #31363f;
  color: white;
}
.dark .heading {
  background-color: #31363f;
}

.aboutContainer .subAboutContainer {
  position: relative;
  z-index: 4;
  box-shadow: 1px 1px 4px var(--maincolor);
  padding: 20px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 40vw;
  overflow: hidden;
}

.subAboutContainer p {
  overflow: hidden;
  height: 100%;
  font-family: iceberg;
  margin-top: 10px;
  font-size: large;
}
.introduction {
  display: flex;
  justify-content: space-around;
}
.information {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}
.introduction,
.information {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
.introduction .information {
  overflow: hidden;
}
.subAboutContainer img {
  height: 10px;
  filter: drop-shadow(1px 1px 1px var(--maincolor));
}
.aboutBox {
  display: flex;
  justify-content: space-around;
  gap: 50px;
}
.aboutBox .aboutImg img {
  width: 50vw;
}
.subAboutBoxes {
  margin-top: 10vh;
}
.introduction h2 {
  font-family: goldman;
}
.aboutImg .msg {
  margin-top: -170px;
  width: 450px;
  text-align: center;
  font-size: 13px;
  color: var(--maincolor);
  font-family: goldman;
}
.dark .aboutImg .msg {
  color: white;
}
/* ************************************* Typing Area *************************** */
.mainContainer {
  width: 100vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 82.5vh;
}
.dark .mainContainer {
  background-color: #31363f;
}

.mainContainer .heading h1 {
  margin: 10px 0;
}
.mainContainer .heading h1,
.mainContainer .heading span {
  font-family: iceberg;
  font-size: xxx-large;
}
.mainContainer .heading h1 span {
  color: var(--maincolor);
}
.container {
  max-width: 80vw;
  margin: 5px;
  width: calc(100% - 10px);
  padding: 30px;
  border-radius: 10px;
  box-sizing: border-box;
  background: #fff;
  box-shadow: 0 0 6px var(--maincolor);
}
.dark .mainContainer .container {
  background-color: #31363f;
  color: white;
}

input.input-field {
  opacity: 0;
  z-index: -999;
  position: absolute;
}

#paragraph {
  word-break: break-all;
  text-align: left;
  margin-bottom: 25px;
  height: 35vh;
}
.section {
  border-radius: 5px;
  padding: 20px;
  padding-top: 0;
}
.section1 {
  border-bottom: 3px solid var(--maincolor);
}
span {
  font-size: 25px;
  user-select: none;
  cursor: tex;
}
span.wrong {
  background-color: yellow !important;
  color: rgb(255, 0, 0) !important;
}

span.correct {
  background-color: yellow !important;
  color: green !important;
}

span.active {
  border-bottom: 5px solid #0a6bf9;
}

.section2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
}
.section2 ul {
  padding-inline-start: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  width: calc(100% - 120px);
}

.section2 ul li {
  list-style-type: none;
  margin: 0 10px;
  font-weight: bold;
}
.section2 ul li:nth-child(2) {
  color: red;
}
.section2 ul li p {
  margin: 5px;
  font-size: 25px;
  color: var(--maincolor);
}

.section2 ul li span {
  font-size: 25px;
  margin-left: 25px;
}
.section2 ul li:nth-child(2) span {
  margin-left: 40px;
}
.section2 button.btn {
  padding: 10px 20px;
  outline: none;
  border: none;
  margin: 5px;
  margin-top: 25px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.5s;
  background-color: white;
  box-shadow: 0 0 8px var(--maincolor);
}

button.btn:hover {
  background-color: var(--maincolor);
  cursor: pointer;
  transition: 0.3s linear;
}
.dark button.btn {
  background-color: #31363f;
  border: none;
  box-shadow: 1px 1px 4px var(--maincolor);
  color: white;
}
.dark button.btn:hover {
  background-color: var(--maincolor);
  box-shadow: 1px 1px 4px var(--maincolor);
}

@media screen and (max-width: 550px) {
  .section2 {
    flex-direction: column;
  }

  .section2 ul {
    width: 100%;
    justify-content: space-between;
  }

  button.btn {
    width: 100%;
    margin-top: 10px;
  }
}

.remArea {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}


/* ****************************************************** Contact ************************************************ */
section {
  height: 82.5vh;
  /* padding: 10rem 7% 2rem; */
}
.dark .contact {
  background-color: #31363f;
}
.contact .heading {
  font-size: 50px;
  font-family: iceberg;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact .heading span {
  color: var(--maincolor);
  margin-left: 15px;
  font-size: 50px;
}
.contact #msg {
  font-size: 1.3rem;
}
.contact form {
  max-width: 60vw;
  margin: 1rem auto;
  text-align: center;
  /* margin-bottom: 3rem; */
}

.contact form .input-box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.contact form .input-box input,
.contact form textarea {
  width: 100%;
  padding: 1.5rem;
  font-size: 1.2rem;
  background: var(--bg-color);
  border-radius: 0.8rem;
  margin: 0.7rem 0;
  box-shadow: 0 0.1rem 0.5rem var(--maincolor);
  border: none;
  outline: none;
}

.contact form .input-box input {
  width: 49%;
  height: 2.5vh;
}

.contact form textarea {
  resize: none;
  height: 30vh;
  font-size: 1.2rem;
}

.contact form .btn {
  /* margin-top: 2rem; */
  cursor: pointer;
}
.dark-mode .contact form .input-box input {
  box-shadow: 2px 2px 6px white;
  opacity: 0.7;
}
.dark-mode .contact form textarea {
  box-shadow: 2px 2px 6px white;
  opacity: 0.7;
}

.subBtn {
  height: 50px;
  width: 150px;
  padding: 10px;
  border-radius: 8px;
  border: none;
  outline: none;
  font-size: 1.1rem;
  margin-top: 8px;
}
.subBtn:hover {
  cursor: pointer;
  background-color: var(--maincolor) !important;
  transition: 0.3s linear;
  color: white !important;
}
.bright .subBtn {
  background-color: white;
  color: black;
  box-shadow: inset 0px 0px 28px var(--maincolor);
}
.dark .subBtn {
  background-color: #31363f;
  color: gray;
  box-shadow: 1px 1px 8px green;
}
@media (max-width: 480px) {
  section {
    height: auto;
  }
  .contact form {
    width: 100vw;
    margin: 0;
  }
.mobtxtarea{
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
}
.mobtxtarea #msg{
  width: 75%;
}
  .contact .input-box {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .input-box input{
    width: 75vw !important;
  }
  .footer{
   margin-top: 20px;
  }
}
